import React from "react";
import { Link, Navigate } from "react-router-dom";

import Header from "../header";
import Footer from "../footer";

class Orders extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            orders: []
        }
    }

    componentDidMount() {
        const { authUser } = this.props;
        authUser && authUser.orders && this.setState({ orders: authUser.orders });
    }

    handleOpenOrder = e => {
        const { id } = e.target.dataset;
        const { orders } = this.state;

        const toggleOrder = orders.map(order => (
            order.id === +id ? { ...order, selected: !order.selected } : { ...order, selected: false }
        ))

        this.setState({ orders: toggleOrder });
    }

    render() {
        const { headerNav, footerNav, isLogged, authUser, handleLogOut } = this.props;
        const { orders } = this.state;

        return (
            isLogged ?
                <>
                    <Header headerNav={headerNav} isLogged={isLogged} authUser={authUser} handleLogOut={handleLogOut} />
                    <main>
                        <div className='orders__flex'>
                            <section className='personal_area'>
                                <nav>
                                    <ul>
                                        <Link to={`/personal-area/${authUser.login.toLowerCase()}/info`} className='private'><li>Личная информация</li></Link>
                                        <Link to={`/personal-area/${authUser.login.toLowerCase()}/orders`} className='orders active'><li>Заказы</li></Link>
                                        <Link to={`/personal-area/${authUser.login.toLowerCase()}/terms`} className='agreement'><li>Условия обслуживания</li></Link>
                                    </ul>
                                </nav>
                                <section className='orders__wrapper'>
                                    {orders.length > 0 ?
                                        orders.map((order, i) => (
                                            <div className='order' key={i}>
                                                <h2 className={order.selected ? 'active' : ''} data-id={order.id} onClick={this.handleOpenOrder} >Заказ № {order.id}</h2>
                                                <p className='order-status'>Не завершён</p>
                                                {order.selected && order[0].map((product, i) => (
                                                    <div className='order__details' key={i}>
                                                        <h3>{product.title}</h3>
                                                        <p>Артикул: {product.article}</p>
                                                        <p>Автозапчасть: {product.quality}</p>
                                                        <p>Артикул: {product.article}</p>
                                                        <p>Количество: {product.quantity} шт.</p>
                                                    </div>
                                                ))
                                                }
                                                <h3>Доставлен в пункт самовывоза</h3>
                                                <p>Принят 14 июля 2022</p>
                                                <p>Количество: {order.totalQuantity} шт. - {order.totalSum} руб.</p>
                                            </div>
                                        ))
                                        :
                                        <h1>У Вас ещё нет заказов</h1>
                                    }
                                </section>
                            </section>
                        </div>
                    </main>
                    <Footer footerNav={footerNav} />
                </>
                :
                <Navigate to='/' replace />
        )
    }
}

export default Orders;