import React from "react";
import SearchForm from "../main/search-form";

class ShoppingCart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id: 0,
            inputsPayMethods: [
                {
                    name: 'cash',
                    value: 'Наличными',
                    selected: false
                },
                {
                    name: 'card',
                    value: 'Картой',
                    selected: false
                }
            ],
            inputsDeliveryMethods: [
                {
                    name: 'pickup',
                    value: 'Самовывоз',
                    selected: false
                },
                {
                    name: 'courier',
                    value: 'Курьерская доставка',
                    selected: false
                }
            ],
            textareaValues: [
                {
                    name: 'address',
                    text: 'Адрес доставки',
                    value: ''
                },
                {
                    name: 'phone',
                    text: 'Контактное лицо',
                    value: ''
                },
                {
                    name: 'comment',
                    text: 'Комментарий к заказу',
                    value: ''
                }
            ],
            orders: []
        }
    }

    componentDidMount() {
        const { authUser } = this.props;
        authUser && authUser.orders && this.setState({ orders: authUser.orders });
    }

    handlePayMethodsClick = e => {
        const { htmlFor } = e.target;

        const { inputsPayMethods } = this.state;

        const chosenPayMethods = inputsPayMethods.map(input => (
            input.name === htmlFor ? { ...input, selected: !input.selected } : { ...input, selected: false }
        ));

        this.setState({ inputsPayMethods: chosenPayMethods });
    }

    handleDeliveryMethodsClick = e => {
        const { htmlFor } = e.target;

        const { inputsDeliveryMethods } = this.state;

        const chosenDeliveryMethods = inputsDeliveryMethods.map(input => (
            input.name === htmlFor ? { ...input, selected: !input.selected } : { ...input, selected: false }
        ))

        this.setState({ inputsDeliveryMethods: chosenDeliveryMethods });
    }

    handleTextareaChange = e => {
        const { name, value } = e.target;
        const { textareaValues } = this.state;

        const values = textareaValues.map(textarea => (
            textarea.name === name ?
                { ...textarea, value: value }
                :
                { ...textarea }
        ))

        this.setState({ textareaValues: values });
    }

    incrementQuantity = e => {
        const { value } = e.target.dataset;
        const { chosenToPurchaseProduct, setChosenProduct } = this.props;
        const setChosenToPurchaseProduct = chosenToPurchaseProduct.map(product => (
            product.article === value ?
                { ...product, quantity: product.quantity < 20 ? product.quantity + 1 : product.quantity }
                :
                { ...product }
        ));

        setChosenProduct(setChosenToPurchaseProduct);
    }

    decrementQuantity = e => {
        const { value } = e.target.dataset;
        const { chosenToPurchaseProduct, setChosenProduct } = this.props;
        const setChosenToPurchaseProduct = chosenToPurchaseProduct.map(product => (
            product.article === value ?
                { ...product, quantity: product.quantity > 1 ? product.quantity - 1 : product.quantity }
                :
                { ...product }
        ));

        setChosenProduct(setChosenToPurchaseProduct);
    }

    handleSubmit = e => {
        e.preventDefault();
        const { chosenToPurchaseProduct, authUser, navigate, setChosenProduct, totalSumOrder, totalQuantityOrder } = this.props;
        const { orders, textareaValues, inputsDeliveryMethods, inputsPayMethods } = this.state;
        const users = JSON.parse(localStorage.getItem('users'));

        const orderId = localStorage.getItem('ordersId');
        localStorage.setItem('ordersId', +orderId + 1);

        let chosenDeliveryMethod = '';
        let chosenPayMethod = '';
        let products = [];
        products.push(chosenToPurchaseProduct);

        inputsPayMethods.map(method => {
            if (method.selected) {
                chosenPayMethod = method.value
            }
            return method;
        })

        inputsDeliveryMethods.map(method => {
            if (method.selected) {
                chosenDeliveryMethod = method.value
            }
            return method;
        })

        orders.push({ selected: false, id: +orderId, ...products, deliveryMethod: chosenDeliveryMethod, payMethod: chosenPayMethod, orderInfo: textareaValues, totalSum: totalSumOrder, totalQuantity: totalQuantityOrder })

        const addOrderToUser = users.map(user => (
            user.login === authUser.login &&
            { ...user, orders: orders }
        ))

        localStorage.setItem('users', JSON.stringify(addOrderToUser));
        setChosenProduct([]);
        navigate('/personal-area/fearex/orders');
    }

    render() {
        const { inputsPayMethods, inputsDeliveryMethods } = this.state;
        const { chosenToPurchaseProduct, totalSumOrder } = this.props;

        return (
            <main>
                <SearchForm {...this.props} />
                <section className='product-cart__flex'>
                    <div className='product-cart__wrapper'>
                        {chosenToPurchaseProduct &&
                            chosenToPurchaseProduct.map((product, i) => (
                                <div className='product-cart' key={i}>
                                    <div className='photo__wrapper'>
                                        <img src={product.photo} alt='product-pic' />
                                    </div>
                                    <div className='product-cart__description'>
                                        <h2>{product.title}</h2>
                                        <p>Артикул: <span>{product.article}</span></p>
                                        <p>Автозапчасть: <span>{product.quality}</span></p>
                                        <p>Производитель: <span>{product.manufacturer}</span></p>
                                        <div className='product-cart__footer'>
                                            <p>{product.price} руб.</p>
                                            <div>
                                                <button data-value={product.article} onClick={this.decrementQuantity}>-</button>
                                                <p>{product.quantity}</p>
                                                <button data-value={product.article} onClick={this.incrementQuantity}>+</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                        <p className='total-price'>Общая стоимость заказа:<span>{totalSumOrder.toFixed(2)} руб.</span></p>
                    </div>
                    <form id='order-info' onSubmit={this.handleSubmit}>
                        <div>
                            <h2>Способ оплаты</h2>
                            {inputsPayMethods.map((method, i) => (
                                <div key={i}>
                                    <input type='checkbox' name={method.name} checked={method.selected} onChange={() => { }} />
                                    <label htmlFor={method.name} onClick={this.handlePayMethodsClick}>{method.value}</label>
                                </div>
                            ))}
                        </div>
                        <div>
                            <h2>Способ доставки</h2>
                            {inputsDeliveryMethods.map((method, i) => (
                                <div key={i}>
                                    <input type='checkbox' name={method.name} checked={method.selected} onChange={() => { }} />
                                    <label htmlFor={method.name} onClick={this.handleDeliveryMethodsClick}>{method.value}</label>
                                </div>
                            ))}
                        </div>
                        <textarea className='address_textarea' placeholder='Адрес доставки' name='address' form='order-info' onChange={this.handleTextareaChange} />
                        <textarea className='phone_textarea' placeholder='Телефон' name='phone' form='order-info' onChange={this.handleTextareaChange} />
                        <textarea className='comment_textarea' placeholder='Коментарий к заказу' name='comment' form='order-info' onChange={this.handleTextareaChange} />
                        <input type='submit' value='Оформить заказ' />
                    </form>
                </section>
            </main>
        )
    }
}

export default ShoppingCart;