import React, { useEffect, useState } from "react";
import { Routes, Route, useNavigate, useLocation } from "react-router-dom";

import Wrapper from "./wrapper";
import AuthorizationContainer from "./components/authorization/authorization-container";
import RegistrationContainer from "./components/authorization/registration-container";
import PrivacyInfo from "./components/personal/privacy-info";
import Orders from "./components/personal/orders";
import TermsService from "./components/personal/terms-service";

const App = (props) => {
  const [isLogged, changeLogged] = useState(null);
  const [authUser, setAuthUser] = useState(null);
  const [totalSumOrder, setTotalSumOrder] = useState(0);
  const [totalQuantityOrder, setTotalQuantityOrder] = useState(0);
  const [chosenToPurchaseProduct, setChosenToPurchaseProduct] = useState([]);
  const navigate = useNavigate();
  const location = useLocation();

  let arrayUsers = JSON.parse(localStorage.getItem('users'));
  if (arrayUsers === null) arrayUsers = [];

  useEffect(() => {
    let arrayUsers = JSON.parse(localStorage.getItem('users'));
    if (arrayUsers === null) arrayUsers = [];

    arrayUsers.map(user => {
      if (user && user.isLogged) {
        setAuthUser(user);
        changeLogged(true);
      }
      return user;
    })
  }, [])

  const registrationSubmit = data => {
    arrayUsers.push({ ...data, isLogged: false });
    localStorage.setItem('users', JSON.stringify(arrayUsers));
  }

  const handleLogIn = (data) => {
    const isLogInUser = arrayUsers.map(user => (
      user.login === data.login ?
        { ...user, isLogged: true }
        :
        { ...user, isLogged: false }
    ))

    localStorage.setItem('users', JSON.stringify(isLogInUser));

    changeLogged(true);
    setAuthUser(data);
  }

  const handleLogOut = () => {
    const isLogOutUser = arrayUsers.map(user => (
      { ...user, isLogged: false }
    ))

    localStorage.setItem('users', JSON.stringify(isLogOutUser));

    changeLogged(false);
    setAuthUser(null);
    navigate('/');
  }

  const handleNewPersonalInfo = (data) => {
    const addPersonalInfo = { ...authUser, ...data };
    const localUsers = JSON.parse(localStorage.getItem('users'));
    const changedLocalUsers = localUsers.map(user => {
      if (user.login === addPersonalInfo.login) {
        return addPersonalInfo;
      } else {
        return user;
      }
    })

    localStorage.setItem('users', JSON.stringify(changedLocalUsers));

    setAuthUser(addPersonalInfo);
  }

  const setChosenProduct = product => {
    let isInArray = false;

    chosenToPurchaseProduct.map(productsArr => {
      if (productsArr.article === product.article) {
        isInArray = true;
        return productsArr.quantity = productsArr.quantity + product.quantity;
      }
      return productsArr;
    })
    !isInArray && chosenToPurchaseProduct.push(product);

    if (Array.isArray(product)) {
      const sum = product.map(item => {
        let sum = item.price * item.quantity;
        setTotalQuantityOrder(item.quantity);
        return sum;
      });
      const quantity = product.map(item => {
        return item.quantity;
      });

      const totalSum = sum.reduce((a, b) => a + b, 0);
      const totalQuantity = quantity.reduce((a, b) => a + b, 0);
      
      setTotalQuantityOrder(totalQuantity);
      setTotalSumOrder(totalSum);
    } else {
      const sum = chosenToPurchaseProduct.map(product => {
        let sum = product.price * product.quantity;
        return sum;
      });
      const quantity = chosenToPurchaseProduct.map(product => {
        return product.quantity;
      });

      const totalSum = sum.reduce((a, b) => a + b, 0);
      const totalQuantity = quantity.reduce((a, b) => a + b, 0);
      
      setTotalQuantityOrder(totalQuantity);
      setTotalSumOrder(totalSum);
    }

    Array.isArray(product) ? setChosenToPurchaseProduct(product) : setChosenToPurchaseProduct(chosenToPurchaseProduct);
  }

  return (
    <div className='wrapper'>
      <Routes>
        <Route path='*' element={<Wrapper
          {...props}
          location={location}
          isLogged={isLogged}
          authUser={authUser}
          chosenToPurchaseProduct={chosenToPurchaseProduct}
          totalQuantityOrder={totalQuantityOrder}
          totalSumOrder={totalSumOrder}
          setChosenProduct={setChosenProduct}
          handleLogOut={handleLogOut}
          navigate={navigate}
        />}
        />
        <Route path='/login/*' element={<AuthorizationContainer
          handleLogIn={handleLogIn}
          isLogged={isLogged}
          authUser={authUser} />}
        />
        <Route path='/registration/*' element={<RegistrationContainer
          registrationSubmit={registrationSubmit}
          arrayUsers={arrayUsers}
          navigate={navigate}
          handleLogIn={handleLogIn} />}
        />
        {isLogged &&
          <>
            <Route path='/personal-area/:user/info/*' element={<PrivacyInfo
              {...props}
              isLogged={isLogged}
              authUser={authUser}
              handleLogOut={handleLogOut}
              handleNewPersonalInfo={handleNewPersonalInfo} />}
            />
            <Route path='/personal-area/:user/orders/*' element={<Orders
              {...props}
              isLogged={isLogged}
              authUser={authUser}
              setChosenProduct={setChosenProduct}
              handleLogOut={handleLogOut} />}
            />
            <Route path='/personal-area/:user/terms/*' element={<TermsService
              {...props}
              isLogged={isLogged}
              authUser={authUser}
              handleLogOut={handleLogOut} />}
            />
          </>
        }
      </Routes>
    </div>
  )
}

export default App;
